import React from 'react';
import { InputGroup, Form, Button } from 'react-bootstrap';
import { debounce } from 'lodash';
import { Celo } from '@dexfair/celo-web-signer';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Token, TokenStore, Dexfair, Settings, actionCreators as storeActions } from '../store/modules/dexfair';

import { TxData } from './Types';
import FormTokenPair from './FormTokenPair';

interface Props {
	celo: Celo;
	network: string;
	isConnected: boolean;
	account: string;
	settings: Settings;
	tokens: TokenStore;
	contract: Dexfair;
	token0: Token;
	token1: Token;
	getAllowance: (token: Token) => Promise<string>;
	setApprove: (token: Token, callback: () => Promise<void>) => void;
	changeToken: (slot: number, token: Token) => void;
	pair: {
		adr: string;
		netProfitBurn?: string;
		netProfitExchange?: string;
		tokens?: string[];
		reserves?: string[];
		balance?: string;
		total?: string;
	};
	exchange: any;
	estimateFee: (gas: number) => string;
	openTxModal: (txData: TxData) => void;
}

const DexfairLiquidityAdd: React.FunctionComponent<Props> = (props) => {
	const [amount0, setAmount0] = React.useState<string>('');
	const [amount1, setAmount1] = React.useState<string>('');
	const [amount2, setAmount2] = React.useState<string>('');

	const [text0, setText0] = React.useState<string>('');
	const [text1, setText1] = React.useState<string>('');
	const [success, setSuccess] = React.useState<string>('');
	const [error, setError] = React.useState<string>('');

	const {
		celo,
		network,
		isConnected,
		account,
		settings,
		tokens,
		token0,
		token1,
		getAllowance,
		setApprove,
		changeToken,
		pair,
		exchange,
		estimateFee,
		openTxModal,
	} = props;

	React.useEffect(() => {
		setAmount0('');
		setAmount1('');
		setText0(updateBalance(tokens[network].balance[token0.address]));
		setText1(updateBalance(tokens[network].balance[token1.address]));
		setError('');
		setSuccess('');
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [network, token0, token1, pair, tokens, account]);

	function updateBalance(balance: string | null | undefined) {
		return balance && account ? `Balance : ${celo.kit.web3.utils.fromWei(balance)}` : '';
	}

	function getAmount1(amount: string) {
		if (pair.total !== undefined) {
			if (pair.reserves && pair.tokens) {
				const reserve0 = pair.tokens[0] === token0.address ? pair.reserves[0] : pair.reserves[1];
				const reserve1 = pair.tokens[1] === token1.address ? pair.reserves[1] : pair.reserves[0];
				const a0 = celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(amount || '0'));
				// const a1 = celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(amount1 || '0'));
				const a1 = a0.mul(celo.kit.web3.utils.toBN(reserve1)).div(celo.kit.web3.utils.toBN(reserve0));
				setAmount1(celo.kit.web3.utils.fromWei(a1));
				return celo.kit.web3.utils.fromWei(a1);
			}
		}
		return '0';
	}

	function calcLp(_amount0: string, _amount1: string) {
		if (pair.total !== undefined) {
			const total = celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(pair.total));
			if (total.eq(celo.kit.web3.utils.toBN(0))) {
				const ONE = celo.kit.web3.utils.toBN(1);
				const TWO = celo.kit.web3.utils.toBN(2);
				const temp = celo.kit.web3.utils
					.toBN(celo.kit.web3.utils.toWei(_amount0 || '0'))
					.mul(celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(_amount1 || '0')));
				let z = temp.add(ONE).div(TWO);
				let sqrt = temp;
				while (z.sub(sqrt).isNeg()) {
					sqrt = z;
					z = temp.div(z).add(z).div(TWO);
				}
				setAmount2(celo.kit.web3.utils.fromWei(sqrt));
			} else if (pair.reserves && pair.tokens) {
				const reserve0 = pair.tokens[0] === token0.address ? pair.reserves[0] : pair.reserves[1];
				const reserve1 = pair.tokens[1] === token1.address ? pair.reserves[1] : pair.reserves[0];
				const a0 = celo.kit.web3.utils
					.toBN(celo.kit.web3.utils.toWei(_amount0 || '0'))
					.mul(total)
					.div(celo.kit.web3.utils.toBN(reserve0));
				const a1 = celo.kit.web3.utils
					.toBN(celo.kit.web3.utils.toWei(_amount1 || '0'))
					.mul(total)
					.div(celo.kit.web3.utils.toBN(reserve1));
				setAmount2(a0.lte(a1) ? celo.kit.web3.utils.fromWei(a0) : celo.kit.web3.utils.fromWei(a1));
			} else {
				setAmount2('');
			}
		} else {
			setAmount2('');
		}
	}

	function getDeadline() {
		return Math.floor(new Date().getTime() / 1000) + parseInt(settings.deadline.value, 10) * 60;
	}

	// eslint-disable-next-line react-hooks/exhaustive-deps
	const delayedQuery = React.useCallback(debounce(estimate, 300), [pair, amount0, amount1]);

	async function estimate(_amount0: string, _amount1: string) {
		if (pair.tokens && isConnected) {
			try {
				const deadline = getDeadline();
				const mintAmounts =
					pair.tokens[0] === token0.address
						? [
								celo.kit.web3.utils.toWei(_amount0 || '0').toString(),
								celo.kit.web3.utils.toWei(_amount1 || '0').toString(),
						  ]
						: [
								celo.kit.web3.utils.toWei(_amount1 || '0').toString(),
								celo.kit.web3.utils.toWei(_amount0 || '0').toString(),
						  ];
				const gas = await exchange.methods
					.liquidityAdd(pair.tokens, mintAmounts, deadline)
					.estimateGas({ from: account });
				setSuccess(`Fee : ${estimateFee(gas)}`);
				setError('');
			} catch (err) {
				// eslint-disable-next-line no-console
				console.log(new Error(err));
				setError(err.message ? err.message : err.Error.toString());
			}
		}
	}

	function onMint() {
		if (pair.tokens) {
			const deadline = getDeadline();
			const mintAmounts =
				pair.tokens[0] === token0.address
					? [celo.kit.web3.utils.toWei(amount0 || '0').toString(), celo.kit.web3.utils.toWei(amount1 || '0').toString()]
					: [
							celo.kit.web3.utils.toWei(amount1 || '0').toString(),
							celo.kit.web3.utils.toWei(amount0 || '0').toString(),
					  ];
			openTxModal({
				title: 'Mint',
				celoTx: {
					from: account,
					to: exchange.options.address,
					data: exchange.methods.liquidityAdd(pair.tokens, mintAmounts, deadline).encodeABI(),
				},
				callback: () => {
					// eslint-disable-next-line no-console
					console.log('Mint 1');
				},
			});
		}
	}

	return (
		<div className="DexfairLiquidityAdd">
			<FormTokenPair
				tokenList={tokens[network].list}
				slot0={{
					token: token0,
					enable: celo.kit.web3.utils.isAddress(pair.adr) && pair.adr !== '0x0000000000000000000000000000000000000000',
					amount: amount0,
					text: text0,
					success: '',
					error: '',
				}}
				slot1={{
					token: token1,
					enable: celo.kit.web3.utils.isAddress(pair.adr) && pair.adr !== '0x0000000000000000000000000000000000000000',
					amount: amount1,
					text: text1,
					success: '',
					error: '',
				}}
				getAllowance={getAllowance}
				setApprove={setApprove}
				updateAmount={(slot: number, amount: string) => {
					switch (slot) {
						case 0:
							setAmount0(amount);
							if (
								pair.total &&
								celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(pair.total)).gt(celo.kit.web3.utils.toBN('0'))
							) {
								setAmount1(getAmount1(amount));
								calcLp(amount, getAmount1(amount));
								delayedQuery(amount, getAmount1(amount));
							} else {
								calcLp(amount, amount1);
								delayedQuery(amount, amount1);
							}
							break;
						case 1:
							setAmount1(amount);
							calcLp(amount0, amount);
							delayedQuery(amount0, amount);
							break;
						default:
							break;
					}
				}}
				changeToken={(slot: number, token: Token) => {
					setAmount2('');
					changeToken(slot, token);
				}}
			/>
			<Form.Group>
				<Form.Text>Liquidity</Form.Text>
				<InputGroup>
					<Form.Control readOnly disabled placeholder="0.0" type="number" value={amount2} />
					<InputGroup.Append>
						<Button
							disabled={!isConnected || error !== '' || success === '' || amount0 === '' || amount1 === ''}
							variant="primary"
							onClick={onMint}
						>
							Mint
						</Button>
					</InputGroup.Append>
				</InputGroup>
				<Form.Text className="text-muted" hidden={success !== '' || error !== ''}>
					{account ? `Balance : ${pair.balance}` : ''}
				</Form.Text>
				<Form.Text className="text-danger">{error}</Form.Text>
				<Form.Text className="text-success" hidden={error !== ''}>
					{success}&nbsp;
				</Form.Text>
			</Form.Group>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(DexfairLiquidityAdd);
