import React from 'react';
import { Button, Form, Dropdown, DropdownButton, InputGroup, Image } from 'react-bootstrap';
import { Celo } from '@dexfair/celo-web-signer';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Token, actionCreators as storeActions } from '../store/modules/dexfair';

interface Props {
	celo: Celo;
	network: string;
	isConnected: boolean;
	tokenList: Token[];
	token: Token;
	label: string;
	enabled: boolean;
	amount: string;
	text?: string;
	success: string;
	error: string;
	updateAmount: (amount: string) => void;
	changeToken: (token: Token) => void;
	getAllowance: (token: Token) => Promise<string>;
	setApprove?: (token: Token, callback: () => Promise<void>) => void;
}

const FormToken: React.FunctionComponent<Props> = (props) => {
	const [approved, setApproved] = React.useState<boolean>(false);

	const {
		celo,
		network,
		isConnected,
		tokenList,
		token,
		label,
		enabled,
		amount,
		text,
		success,
		error,
		getAllowance,
		setApprove,
		updateAmount,
		changeToken,
	} = props;

	React.useEffect(() => {
		updateApproved();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [network, enabled, token]);

	async function updateApproved() {
		if (token.address) {
			const result = await getAllowance(token);
			setApproved(celo.kit.web3.utils.toBN(result).gt(celo.kit.web3.utils.toBN('0')));
		}
	}

	function getList() {
		const items = tokenList.map((element, index) => (
			<Dropdown.Item key={`T_${index.toString()}`} eventKey={JSON.stringify(element)}>
				{element.symbol}
			</Dropdown.Item>
		));
		return items;
	}

	return (
		<div className="FormToken">
			<Form.Group>
				<Form.Text>{label}</Form.Text>
				<InputGroup>
					<InputGroup.Prepend>
						<InputGroup.Text>
							<Image hidden={!token.icon} src={token ? token.icon : ''} style={{ height: '24px' }} />
							<i hidden={!!token.icon} className="fas fa-question-circle" />
						</InputGroup.Text>
						<Button
							variant="danger"
							hidden={approved || !isConnected}
							onClick={() => {
								if (setApprove) {
									setApprove(token, async () => {
										await updateApproved();
									});
								}
							}}
						>
							<i className="fas fa-lock" />
						</Button>
					</InputGroup.Prepend>
					<Form.Control
						type="number"
						placeholder="0.0"
						readOnly={!enabled}
						disabled={!enabled}
						value={amount}
						onChange={(e) => {
							updateAmount(e.target.value);
						}}
					/>
					<DropdownButton
						hidden={tokenList.length === 0 || !token || !token.symbol}
						as={InputGroup.Append}
						variant="outline-secondary"
						title={token ? token.symbol : ''}
						onSelect={async (e: string | null) => {
							if (e) {
								const temp: Token = JSON.parse(e);
								if (temp.address !== token.address) {
									changeToken(temp);
								}
							}
						}}
					>
						{getList()}
					</DropdownButton>
				</InputGroup>
				<Form.Text className="text-danger">{error}</Form.Text>
				<Form.Text className="text-success" hidden={error !== ''}>
					{success}
				</Form.Text>
				<Form.Text className="text-muted" hidden={success !== '' || error !== ''}>
					{text}&nbsp;
				</Form.Text>
			</Form.Group>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(FormToken);
