import React from 'react';
import { InputGroup, Form, Button } from 'react-bootstrap';
import { Celo } from '@dexfair/celo-web-signer';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Token, TokenStore, Dexfair, Settings, actionCreators as storeActions } from '../store/modules/dexfair';

import FormTokenPair from './FormTokenPair';

interface Props {
	celo: Celo;
	network: string;
	isConnected: boolean;
	account: string;
	settings: Settings;
	tokens: TokenStore;
	contract: Dexfair;
	token0: Token;
	token1: Token;
	getAllowance: (token: Token) => Promise<string>;
	setApprove: (token: Token, callback: () => Promise<void>) => void;
	changeToken: (slot: number, token: Token) => void;
	createPair: (callback: () => void) => void;
	pair: {
		adr: string;
		netProfitBurn?: string;
		netProfitExchange?: string;
		tokens?: string[];
		reserves?: string[];
		balance?: string;
		total?: string;
	};
}

const DexfairLiquidityStatus: React.FunctionComponent<Props> = (props) => {
	const [amount0, setAmount0] = React.useState<string>('');
	const [amount1, setAmount1] = React.useState<string>('');

	const {
		celo,
		network,
		isConnected,
		account,
		tokens,
		token0,
		token1,
		getAllowance,
		setApprove,
		changeToken,
		createPair,
		pair,
	} = props;

	React.useEffect(() => {
		if (pair.adr && pair.adr !== '0x0000000000000000000000000000000000000000' && pair.tokens && pair.reserves) {
			if (pair.tokens[0] === token0.address) {
				setAmount0(celo.kit.web3.utils.fromWei(pair.reserves[0]));
				setAmount1(celo.kit.web3.utils.fromWei(pair.reserves[1]));
			} else {
				setAmount0(celo.kit.web3.utils.fromWei(pair.reserves[1]));
				setAmount1(celo.kit.web3.utils.fromWei(pair.reserves[0]));
			}
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [token0, token1, pair, account]);

	return (
		<div className="DexfairLiquidityStatus">
			<FormTokenPair
				tokenList={tokens[network].list}
				slot0={{ token: token0, enable: false, amount: amount0, success: '', error: '' }}
				slot1={{ token: token1, enable: false, amount: amount1, success: '', error: '' }}
				getAllowance={getAllowance}
				setApprove={setApprove}
				updateAmount={(slot: number, amount: string) => {
					// EMPTY
				}}
				changeToken={changeToken}
			/>
			<Form.Group>
				<Form.Text>Total Supply</Form.Text>
				<InputGroup>
					<Form.Control value={pair.total} readOnly disabled />
				</InputGroup>
			</Form.Group>
			<Form.Group hidden={!isConnected}>
				<Form.Text>Liquidity</Form.Text>
				<InputGroup>
					<Form.Control value={pair.balance} readOnly disabled />
				</InputGroup>
			</Form.Group>
			<Button
				variant={isConnected ? 'primary' : 'secondary'}
				disabled={celo.kit.web3.utils.isAddress(pair.adr) || pair.adr === '0x0000000000000000000000000000000000000000'}
				block
				onClick={() => {
					createPair(() => {
						// eslint-disable-next-line no-console
						console.log('Pair Create..');
					});
				}}
			>
				Crate Pair
			</Button>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(DexfairLiquidityStatus);
