import React from 'react';
import { InputGroup, Form, Button } from 'react-bootstrap';
import { debounce } from 'lodash';
import { Celo } from '@dexfair/celo-web-signer';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Token, TokenStore, Dexfair, Settings, actionCreators as storeActions } from '../store/modules/dexfair';

import { TxData } from './Types';
import FormTokenPair from './FormTokenPair';

interface Props {
	celo: Celo;
	network: string;
	isConnected: boolean;
	account: string;
	settings: Settings;
	tokens: TokenStore;
	contract: Dexfair;
	token0: Token;
	token1: Token;
	getAllowance: (token: Token) => Promise<string>;
	setApprove: (token: Token, callback: () => Promise<void>) => void;
	changeToken: (slot: number, token: Token) => void;
	pair: {
		adr: string;
		netProfitBurn?: string;
		netProfitExchange?: string;
		tokens?: string[];
		reserves?: string[];
		balance?: string;
		total?: string;
	};
	exchange: any;
	estimateFee: (gas: number) => string;
	openTxModal: (txData: TxData) => void;
}

const DexfairLiquidityRemove: React.FunctionComponent<Props> = (props) => {
	const [amount0, setAmount0] = React.useState<string>('');
	const [amount1, setAmount1] = React.useState<string>('');
	const [amount2, setAmount2] = React.useState<string>('');

	const [text0, setText0] = React.useState<string>('');
	const [text1, setText1] = React.useState<string>('');
	const [success, setSuccess] = React.useState<string>('');
	const [error, setError] = React.useState<string>('');

	const {
		celo,
		network,
		isConnected,
		account,
		settings,
		tokens,
		token0,
		token1,
		getAllowance,
		setApprove,
		changeToken,
		pair,
		exchange,
		estimateFee,
		openTxModal,
	} = props;

	React.useEffect(() => {
		setAmount0('');
		setAmount1('');
		setText0(updateBalance(tokens[network].balance[token0.address]));
		setText1(updateBalance(tokens[network].balance[token1.address]));
		setError('');
		setSuccess('');
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [network, token0, token1, pair, tokens, account]);

	function updateBalance(balance: string | null | undefined) {
		return balance && account ? `Balance : ${celo.kit.web3.utils.fromWei(balance)}` : '';
	}

	function getDeadline() {
		return Math.floor(new Date().getTime() / 1000) + parseInt(settings.deadline.value, 10) * 60;
	}

	// eslint-disable-next-line react-hooks/exhaustive-deps
	const delayedQuery = React.useCallback(debounce(estimate, 300), [pair, amount2]);

	async function estimate(amount: string) {
		if (pair.tokens && isConnected) {
			try {
				const deadline = getDeadline();
				const burnAmount = celo.kit.web3.utils.toWei(amount).toString();
				const gas = await exchange.methods
					.liquidityRemove(pair.tokens, burnAmount, deadline)
					.estimateGas({ from: account });
				setSuccess(`Fee : ${estimateFee(gas)}`);
				setError('');
			} catch (err) {
				// eslint-disable-next-line no-console
				console.log(new Error(err));
				setError(err.message ? err.message : err.Error.toString());
			}
		}
	}

	function onBurn() {
		const deadline = getDeadline();
		const burnAmount = celo.kit.web3.utils.toWei(amount2).toString();
		openTxModal({
			title: 'Burn',
			celoTx: {
				from: account,
				to: exchange.options.address,
				data: exchange.methods.liquidityRemove(pair.tokens, burnAmount, deadline).encodeABI(),
			},
			callback: () => {
				// eslint-disable-next-line no-console
				console.log('Burn 1');
			},
		});
	}

	return (
		<div className="DexfairLiquidityRemove">
			<FormTokenPair
				tokenList={tokens[network].list}
				slot0={{
					token: token0,
					enable: celo.kit.web3.utils.isAddress(pair.adr) && pair.adr !== '0x0000000000000000000000000000000000000000',
					amount: amount0,
					text: text0,
					success: '',
					error: '',
				}}
				slot1={{
					token: token1,
					enable: celo.kit.web3.utils.isAddress(pair.adr) && pair.adr !== '0x0000000000000000000000000000000000000000',
					amount: amount1,
					text: text1,
					success: '',
					error: '',
				}}
				getAllowance={getAllowance}
				setApprove={setApprove}
				updateAmount={(slot: number, amount: string) => {
					switch (slot) {
						case 0:
							setAmount0(amount);
							break;
						case 1:
							setAmount1(amount);
							break;
						default:
							break;
					}
				}}
				changeToken={(slot: number, token: Token) => {
					setAmount2('');
					changeToken(slot, token);
				}}
			/>
			<Form.Group>
				<Form.Text>Liquidity</Form.Text>
				<InputGroup>
					<Form.Control
						readOnly={!isConnected}
						disabled={
							!isConnected ||
							celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(pair.balance || '0')).eq(celo.kit.web3.utils.toBN('0'))
						}
						placeholder="0.0"
						type="number"
						value={amount2}
						onChange={(e) => {
							setAmount2(e.target.value);
							if (
								e.target.value &&
								pair.total &&
								pair.balance &&
								pair.tokens &&
								pair.reserves &&
								celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(pair.total)).gt(celo.kit.web3.utils.toBN('0')) &&
								celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(pair.balance)).gt(celo.kit.web3.utils.toBN('0'))
							) {
								const total = celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(pair.total));
								const balance = celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(pair.balance));
								const burn = celo.kit.web3.utils.toBN(celo.kit.web3.utils.toWei(e.target.value));
								const temp0 = celo.kit.web3.utils.fromWei(
									celo.kit.web3.utils
										.toBN(pair.reserves[0])
										.mul(burn.lte(balance) ? burn : balance)
										.div(total)
										.toString()
								);
								const temp1 = celo.kit.web3.utils.fromWei(
									celo.kit.web3.utils
										.toBN(pair.reserves[1])
										.mul(burn.lte(balance) ? burn : balance)
										.div(total)
										.toString()
								);
								if (token0.address === pair.tokens[0]) {
									setAmount0(temp0);
									setAmount1(temp1);
								} else {
									setAmount0(temp1);
									setAmount1(temp0);
								}
								delayedQuery(e.target.value);
							} else {
								setAmount0('');
								setAmount1('');
								setSuccess('');
								setError('');
							}
						}}
					/>
					<InputGroup.Append>
						<Button
							disabled={
								!isConnected ||
								amount2 === '' ||
								celo.kit.web3.utils
									.toBN(celo.kit.web3.utils.toWei(pair.balance || '0'))
									.eq(celo.kit.web3.utils.toBN('0'))
							}
							variant="primary"
							onClick={onBurn}
						>
							Burn
						</Button>
					</InputGroup.Append>
				</InputGroup>
				<Form.Text className="text-muted" hidden={success !== '' || error !== ''}>
					{account ? `Balance : ${pair.balance}` : ''}
				</Form.Text>
				<Form.Text className="text-danger">{error}</Form.Text>
				<Form.Text className="text-success" hidden={error !== ''}>
					{success}&nbsp;
				</Form.Text>
			</Form.Group>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(DexfairLiquidityRemove);
