import React from 'react';
import { Tab, Row, Col, Nav } from 'react-bootstrap';
import { Celo } from '@dexfair/celo-web-signer';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Token, TokenStore, Dexfair, Settings, actionCreators as storeActions } from '../store/modules/dexfair';

import { TxData } from './Types';
import DexfairLiquidityStatus from './DexfairLiquidityStatus';
import DexfairLiquidityMint from './DexfairLiquidityMint';
import DexfairLiquidityBurn from './DexfairLiquidityBurn';

interface Props {
	celo: Celo;
	network: string;
	isConnected: boolean;
	account: string;
	settings: Settings;
	tokens: TokenStore;
	contract: Dexfair;
	hidden: boolean;
	getAllowance: (addrContract: string, addrToken: string) => Promise<string>;
	setApprove: (addrContract: string, addrToken: string, callback: () => Promise<void>) => void;
	estimateFee: (gas: number) => string;
	openTxModal: (txData: TxData) => void;
}

const DexfairLiquidity: React.FunctionComponent<Props> = (props) => {
	const [token0, setToken0] = React.useState<Token>({ icon: '', symbol: '', address: '' });
	const [token1, setToken1] = React.useState<Token>({ icon: '', symbol: '', address: '' });

	const [key, setKey] = React.useState<string | null>('status');

	const [exchange, setExchange] = React.useState<any>(null);
	const [pair, setPair] = React.useState<{
		adr: string;
		netProfitBurn?: string;
		netProfitExchange?: string;
		tokens?: string[];
		reserves?: string[];
		balance?: string;
		total?: string;
	}>({ adr: '' });

	const {
		celo,
		network,
		isConnected,
		account,
		tokens,
		contract,
		hidden,
		getAllowance,
		setApprove,
		estimateFee,
		openTxModal,
	} = props;

	React.useEffect(() => {
		if (tokens[network].list.length > 1) {
			const address = contract.address[network];
			if (!exchange && address) {
				const temp = new celo.kit.web3.eth.Contract(contract.abi, address);
				setExchange(temp);
			} else if (exchange) {
				setToken0(tokens[network].list[0]);
				setToken1(tokens[network].list[1]);
				updatePair(tokens[network].list[0], tokens[network].list[1]);
			}
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [network, tokens, exchange, account]);

	async function updatePair(tokenA: Token, tokenB: Token) {
		if (exchange) {
			const pairStatus = await exchange.methods.getPair([tokenA.address, tokenB.address]).call();
			const total = await celo.kit.web3.eth.call({
				to: pairStatus.adr,
				data: celo.contracts.erc20.methods.totalSupply().encodeABI(),
			});
			if (account) {
				const balance = await celo.kit.web3.eth.call({
					to: pairStatus.adr,
					data: celo.contracts.erc20.methods.balanceOf(account).encodeABI(),
				});
				if (balance !== '0x') {
					setPair({
						...pairStatus,
						balance: celo.kit.web3.utils.fromWei(celo.kit.web3.eth.abi.decodeParameter('uint256', balance).toString()),
						total: celo.kit.web3.utils.fromWei(celo.kit.web3.eth.abi.decodeParameter('uint256', total).toString()),
					});
				} else {
					setPair({ adr: '' });
				}
			} else {
				setPair({
					...pairStatus,
					total: celo.kit.web3.utils.fromWei(celo.kit.web3.eth.abi.decodeParameter('uint256', total).toString()),
				});
			}
		} else {
			setPair({ adr: '' });
		}
	}

	function createPair(callback: () => void) {
		openTxModal({
			title: 'Create Pair',
			celoTx: {
				from: account,
				to: exchange.options.address,
				data: exchange.methods.register([token0.address, token1.address]).encodeABI(),
			},
			callback,
		});
	}

	function changeToken(slot: number, token: Token) {
		switch (slot) {
			case 0:
				if (token1.address === token.address) {
					setToken1(token0);
					setToken0(token);
				} else {
					setToken0(token);
					updatePair(token, token1);
				}
				break;
			case 1:
				setToken1(token);
				if (token0.address === token.address) {
					setToken0(token1);
					setToken1(token);
				} else {
					setToken1(token);
					updatePair(token0, token);
				}
				break;
			default:
				break;
		}
	}

	return (
		<div className="DexfairLiquidity" hidden={hidden}>
			<Tab.Container defaultActiveKey={key}>
				<Row>
					<Col sm={2}>
						<Nav variant="pills" className="flex-column">
							<Nav.Item>
								<Nav.Link eventKey="status" className="text-center" onSelect={(k) => setKey(k)}>
									Status
								</Nav.Link>
							</Nav.Item>
							<Nav.Item>
								<Nav.Link eventKey="mint" className="text-center" onSelect={(k) => setKey(k)} disabled={!isConnected}>
									Mint
								</Nav.Link>
							</Nav.Item>
							<Nav.Item>
								<Nav.Link eventKey="burn" className="text-center" onSelect={(k) => setKey(k)} disabled={!isConnected}>
									Burn
								</Nav.Link>
							</Nav.Item>
						</Nav>
					</Col>
					<Col sm={10}>
						<Tab.Content>
							<Tab.Pane eventKey="status">
								<DexfairLiquidityStatus
									token0={token0}
									token1={token1}
									getAllowance={async (token: Token) => {
										const result = await getAllowance(exchange.options.address, token.address);
										return result;
									}}
									setApprove={(token: Token, callback) => {
										setApprove(exchange.options.address, token.address, callback);
									}}
									changeToken={changeToken}
									createPair={createPair}
									pair={pair}
								/>
							</Tab.Pane>
							<Tab.Pane eventKey="mint">
								<DexfairLiquidityMint
									token0={token0}
									token1={token1}
									getAllowance={async (token: Token) => {
										const result = await getAllowance(exchange.options.address, token.address);
										return result;
									}}
									setApprove={(token: Token, callback) => {
										setApprove(exchange.options.address, token.address, callback);
									}}
									changeToken={changeToken}
									pair={pair}
									exchange={exchange}
									estimateFee={estimateFee}
									openTxModal={openTxModal}
								/>
							</Tab.Pane>
							<Tab.Pane eventKey="burn">
								<DexfairLiquidityBurn
									token0={token0}
									token1={token1}
									getAllowance={async (token: Token) => {
										const result = await getAllowance(exchange.options.address, token.address);
										return result;
									}}
									setApprove={(token: Token, callback) => {
										setApprove(exchange.options.address, token.address, callback);
									}}
									changeToken={changeToken}
									pair={pair}
									exchange={exchange}
									estimateFee={estimateFee}
									openTxModal={openTxModal}
								/>
							</Tab.Pane>
						</Tab.Content>
					</Col>
				</Row>
			</Tab.Container>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(DexfairLiquidity);
