import React from 'react';
import { Button } from 'react-bootstrap';
import { debounce } from 'lodash';
import { Celo } from '@dexfair/celo-web-signer';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Token, TokenStore, Settings, actionCreators as storeActions } from '../store/modules/dexfair';

import { TxData } from './Types';
import FormTokenPair from './FormTokenPair';

interface Props {
	celo: Celo;
	network: string;
	isConnected: boolean;
	account: string;
	settings: Settings;
	tokens: TokenStore;
	hidden: boolean;
	getAllowance: (addrContract: string, addrToken: string) => Promise<string>;
	setApprove: (addrContract: string, addrToken: string, callback: () => Promise<void>) => void;
	estimateFee: (gas: number) => string;
	openTxModal: (txData: TxData) => void;
}

const Exchange: React.FunctionComponent<Props> = (props) => {
	const [tokenList, setTokenList] = React.useState<Token[]>([]);
	const [token0, setToken0] = React.useState<Token>({ icon: '', symbol: '', address: '' });
	const [token1, setToken1] = React.useState<Token>({ icon: '', symbol: '', address: '' });
	const [amount0, setAmount0] = React.useState<string>('');
	const [amount1, setAmount1] = React.useState<string>('');
	const [success0, setSuccess0] = React.useState<string>('');
	const [error0, setError0] = React.useState<string>('');
	const [exchange, setExchange] = React.useState<any>(null);

	const {
		celo,
		network,
		isConnected,
		account,
		settings,
		tokens,
		hidden,
		getAllowance,
		setApprove,
		estimateFee,
		openTxModal,
	} = props;

	React.useEffect(() => {
		async function init() {
			if (!exchange) {
				const temp = await celo.kit.contracts.getExchange();
				setExchange(temp);
				setTokenList([tokens[network].list[0], tokens[network].list[1]]);
				setToken0(tokens[network].list[0]);
				setToken1(tokens[network].list[1]);
			}
		}
		if (tokens[network].list.length > 1) {
			init();
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [network, tokens]);

	// eslint-disable-next-line react-hooks/exhaustive-deps
	const delayedQuery = React.useCallback(debounce(getEstimate, 300), [amount0, token0, token1]);

	async function getEstimate(
		tokenSell: Token,
		sellAmount: string,
		// eslint-disable-next-line @typescript-eslint/no-unused-vars
		tokenBuy: Token
	) {
		if (sellAmount !== '') {
			const sell = celo.kit.web3.utils.toWei(sellAmount).toString();
			const sellGold = tokenSell.address === tokenList[0].address;
			const buyAmount = await exchange.contract.methods.getBuyTokenAmount(sell, sellGold).call();

			const buy = celo.kit.web3.utils
				.toBN(buyAmount.toString())
				.mul(
					celo.kit.web3.utils.toBN(
						parseInt((1000 - parseInt(settings.slippage.value, 10) * 10).toString(), 10).toString()
					)
				)
				.div(celo.kit.web3.utils.toBN(1000))
				.toString();

			if (celo.kit.web3.utils.isAddress(account)) {
				try {
					const gas = await celo.kit.web3.eth.estimateGas({
						from: account,
						to: exchange.address,
						data: exchange.contract.methods.exchange(sell, buy, sellGold).encodeABI(),
					});

					if (tokenSell.address === tokenList[0].address) {
						const total = celo.kit.web3.utils.toBN(sell).add(celo.kit.web3.utils.toBN(gas));
						const balance = celo.kit.web3.utils.toBN(await celo.kit.web3.eth.getBalance(account)); // TODO: redux getBalance
						if (balance.lt(total)) {
							setAmount1(celo.kit.web3.utils.fromWei(buyAmount.toString()));
							setError0('out of balance');
							return;
						}
					}
					setAmount1(celo.kit.web3.utils.fromWei(buyAmount.toString()));
					setSuccess0(`fee : ${estimateFee(gas)}`);
					setError0('');
				} catch (e) {
					setAmount1(celo.kit.web3.utils.fromWei(buyAmount.toString()));
					setError0(e.message ? e.message : e.Error.toString());
				}
			} else {
				setAmount1(celo.kit.web3.utils.fromWei(buyAmount.toString()));
				setError0('');
			}
		} else {
			setAmount1('');
			setSuccess0('');
			setError0('');
		}
	}

	function onExchange() {
		if (celo.kit.web3.utils.isAddress(account) && amount0 !== '') {
			const sell = celo.kit.web3.utils.toWei(amount0);
			const buy = celo.kit.web3.utils
				.toBN(celo.kit.web3.utils.toWei(amount1))
				.mul(
					celo.kit.web3.utils.toBN(
						parseInt((1000 - parseInt(settings.slippage.value, 10) * 10).toString(), 10).toString()
					)
				)
				.div(celo.kit.web3.utils.toBN(1000))
				.toString();
			const sellGold = token0.address === tokens[network].list[0].address;
			openTxModal({
				title: 'Exchange',
				celoTx: {
					from: account,
					to: exchange.address,
					data: exchange.contract.methods.exchange(sell, buy, sellGold).encodeABI(),
				},
				callback: () => {
					console.log('exchange');
				},
			});
		}
	}

	return (
		<div className="Exchange" hidden={hidden}>
			<FormTokenPair
				tokenList={tokenList}
				slot0={{ label: 'Sell', token: token0, enable: true, amount: amount0, success: success0, error: error0 }}
				slot1={{ label: 'Buy (estimated)', token: token1, enable: false, amount: amount1, success: '', error: '' }}
				getAllowance={async (token: Token) => {
					const result = await getAllowance(exchange.address, token.address);
					return result;
				}}
				setApprove={(token: Token, callback) => {
					setApprove(exchange.address, token.address, callback);
				}}
				updateAmount={(slot: number, amount: string) => {
					switch (slot) {
						case 0:
							setAmount0(amount);
							delayedQuery(token0, amount, token1);
							break;
						default:
							break;
					}
					return { amount: '', success: '', error: '' };
				}}
				changeToken={(slot: number, token: Token) => {
					switch (slot) {
						case 0:
							if (token1.address === token.address) {
								setToken1(token0);
								setToken0(token);
							} else {
								setToken0(token);
							}
							break;
						case 1:
							setToken1(token);
							if (token0.address === token.address) {
								setToken0(token1);
								setToken1(token);
							} else {
								setToken1(token);
							}
							break;
						default:
							break;
					}
					setAmount0('');
					setAmount1('');
					setSuccess0('');
					setError0('');
				}}
			/>
			<Button
				variant={isConnected ? 'primary' : 'secondary'}
				onClick={onExchange}
				disabled={!isConnected || error0 !== '' || amount0 === '' || success0 === ''}
				block
			>
				Exchange
			</Button>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(Exchange);
