import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Settings, actionCreators as storeActions } from '../store/modules/dexfair';

interface Props {
	settings: Settings;
	show: boolean;
	onHide: () => void;
	StoreActions: typeof storeActions;
}

const ModalSettings: React.FunctionComponent<Props> = (props) => {
	const [header, setHeader] = React.useState<string>('');
	const [slippage, setSlippage] = React.useState<string>('');
	const [deadline, setDeadline] = React.useState<string>('');

	const { settings, show, onHide, StoreActions } = props;

	React.useEffect(() => {
		setSlippage(settings.slippage.value);
		setDeadline(settings.deadline.value);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [show]);

	return (
		<div className="ModalSettings">
			<Modal show={show} onHide={onHide} aria-labelledby="contained-modal-title-vcenter" backdrop="static" centered>
				<Modal.Header closeButton className={header}>
					<Modal.Title id="contained-modal-title-vcenter">Settings</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					<Form.Group>
						<Form.Text>{settings.slippage.name}</Form.Text>
						<InputGroup>
							<Form.Control
								type="string"
								placeholder=""
								value={slippage}
								isInvalid={parseInt(slippage, 10) < 50 || parseInt(slippage, 10) > 100}
								onChange={(e) => {
									const value = parseInt(e.target.value, 10);
									setSlippage(e.target.value);
									if (value >= 50 && value <= 100) {
										StoreActions.dexfairSetting('slippage', e.target.value);
									}
								}}
							/>
							<InputGroup.Append>
								<InputGroup.Text>{settings.slippage.unit}</InputGroup.Text>
							</InputGroup.Append>
						</InputGroup>
						<Form.Text className="text-muted">{settings.slippage.desc}</Form.Text>
					</Form.Group>
					<Form.Group>
						<Form.Text>{settings.deadline.name}</Form.Text>
						<InputGroup>
							<Form.Control
								type="string"
								placeholder=""
								value={deadline}
								isInvalid={parseInt(deadline, 10) < 5 || parseInt(deadline, 10) > 60}
								onChange={(e) => {
									const value = parseInt(e.target.value, 10);
									setDeadline(e.target.value);
									if (value >= 50 && value <= 100) {
										StoreActions.dexfairSetting('deadline', e.target.value);
									}
								}}
							/>
							<InputGroup.Append>
								<InputGroup.Text>{settings.deadline.unit}</InputGroup.Text>
							</InputGroup.Append>
						</InputGroup>
						<Form.Text className="text-muted">{settings.deadline.desc}</Form.Text>
					</Form.Group>
				</Modal.Body>
				<Modal.Footer>
					<Button onClick={onHide}>Close</Button>
				</Modal.Footer>
			</Modal>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(ModalSettings);
