import { AbiItem } from 'web3-utils';
import { Celo, NETWORKS } from '@dexfair/celo-web-signer';

export interface Token {
	icon: string;
	symbol: string;
	address: string;
	name?: string;
}

export interface TokenStore {
	[network: string]: {
		list: Token[];
		balance: { [address: string]: string | null | undefined };
	};
}

export interface Dexfair {
	address: { [network: string]: string | null | undefined };
	abi: AbiItem[];
}

export interface Settings {
	[key: string]: {
		name: string;
		desc: string;
		unit: string;
		value: string;
	};
}

export interface DexfairStore {
	celo: Celo;
	network: string;
	isConnected: boolean;
	account: string;
	contract: Dexfair;
	settings: Settings;
	tokens: TokenStore;
}

export const NETWORK = 'celo/NETWORK';
export const CONNECT = 'celo/CONNECT';
export const ACCOUNT = 'celo/ACCOUNT';
export const DEXFAIR = 'celo/DEXFAIR';
export const SETTING = 'celo/SETTING';
export const INSERT = 'tokens/INSERT';
export const REMOVE = 'tokens/REMOVE';
export const UPDATE = 'tokens/UPDATE';

interface NetworkUpdate {
	type: typeof NETWORK;
	meta: {
		network: string;
	};
}

interface NetworkConnect {
	type: typeof CONNECT;
	meta: {
		connection: boolean;
	};
}

interface AccountUpdate {
	type: typeof ACCOUNT;
	meta: {
		address: string;
	};
}

interface DexfairUpdate {
	type: typeof DEXFAIR;
	payload: Dexfair;
}

interface DexfairSetting {
	type: typeof SETTING;
	meta: {
		key: string;
		value: string;
	};
}

interface TokenInsert {
	type: typeof INSERT;
	meta: {
		network: string;
		token: Token;
	};
}

interface TokenUpdate {
	type: typeof UPDATE;
	meta: {
		network: string;
		address: string;
		balance: string;
	};
}

interface TokenRemove {
	type: typeof REMOVE;
	meta: {
		network: string;
		address: string;
	};
}

function networkUpdate(network: string): NetworkUpdate {
	return {
		type: NETWORK,
		meta: {
			network,
		},
	};
}

function networkConnect(connection: boolean): NetworkConnect {
	return {
		type: CONNECT,
		meta: {
			connection,
		},
	};
}

function accountUpdate(address: string): AccountUpdate {
	return {
		type: ACCOUNT,
		meta: {
			address,
		},
	};
}

function dexfairUpdate(dexfair: {
	address: { [key: string]: string | null | undefined };
	abi: AbiItem[];
}): DexfairUpdate {
	return {
		type: DEXFAIR,
		payload: dexfair,
	};
}

function dexfairSetting(key: string, value: string): DexfairSetting {
	return {
		type: SETTING,
		meta: {
			key,
			value,
		},
	};
}

function tokenInsert(network: string, token: Token): TokenInsert {
	return {
		type: INSERT,
		meta: {
			network,
			token,
		},
	};
}

function tokenUpdate(network: string, address: string, balance: string): TokenUpdate {
	return {
		type: UPDATE,
		meta: {
			network,
			address,
			balance,
		},
	};
}

function tokenRemove(network: string, address: string): TokenRemove {
	return {
		type: REMOVE,
		meta: {
			network,
			address,
		},
	};
}

export type TokenActionTypes =
	| NetworkUpdate
	| NetworkConnect
	| AccountUpdate
	| DexfairUpdate
	| DexfairSetting
	| TokenInsert
	| TokenUpdate
	| TokenRemove;
export const actionCreators = {
	networkUpdate,
	networkConnect,
	accountUpdate,
	dexfairUpdate,
	dexfairSetting,
	tokenInsert,
	tokenUpdate,
	tokenRemove,
};

const initialState: DexfairStore = {
	celo: new Celo(NETWORKS.Mainnet),
	network: 'Mainnet',
	isConnected: false,
	account: '',
	contract: { address: {}, abi: [] },
	settings: {
		slippage: {
			name: 'Slippage tolerance',
			desc: 'Transaction will revert if the price is too expensive.',
			unit: '%',
			value: '95',
		},
		deadline: {
			name: 'Transaction deadline',
			desc: 'Transaction will revert if pending is too long.',
			unit: 'min',
			value: '5',
		},
	},
	tokens: {
		Mainnet: {
			list: [],
			balance: {},
		},
		Alfajores: {
			list: [],
			balance: {},
		},
		Baklava: {
			list: [],
			balance: {},
		},
	},
};

export function dexfairStoreReducer(state = initialState, action: TokenActionTypes): DexfairStore {
	switch (action.type) {
		case NETWORK:
			return {
				celo: state.celo,
				network: action.meta.network,
				isConnected: state.isConnected,
				account: state.account,
				contract: state.contract,
				settings: state.settings,
				tokens: state.tokens,
			};
		case CONNECT:
			return {
				celo: state.celo,
				network: state.network,
				isConnected: action.meta.connection,
				account: state.account,
				contract: state.contract,
				settings: state.settings,
				tokens: state.tokens,
			};
		case ACCOUNT:
			return {
				celo: state.celo,
				network: state.network,
				isConnected: state.isConnected,
				account: action.meta.address,
				contract: state.contract,
				settings: state.settings,
				tokens: state.tokens,
			};
		case DEXFAIR:
			return {
				celo: state.celo,
				network: state.network,
				isConnected: state.isConnected,
				account: state.account,
				contract: action.payload,
				settings: state.settings,
				tokens: state.tokens,
			};
		case SETTING:
			return {
				celo: state.celo,
				network: state.network,
				isConnected: state.isConnected,
				account: state.account,
				contract: state.contract,
				settings: {
					...state.settings,
					[action.meta.key]: { ...state.settings[action.meta.key], value: action.meta.value },
				},
				tokens: state.tokens,
			};
		case INSERT:
			return {
				celo: state.celo,
				network: state.network,
				isConnected: state.isConnected,
				account: state.account,
				contract: state.contract,
				settings: state.settings,
				tokens: {
					...state.tokens,
					[action.meta.network]: {
						list: [...state.tokens[state.network].list, action.meta.token],
						balance: {
							...state.tokens[action.meta.network].balance,
							[action.meta.token.address]: '',
						},
					},
				},
			};
		case UPDATE:
			return {
				celo: state.celo,
				network: state.network,
				isConnected: state.isConnected,
				account: state.account,
				contract: state.contract,
				settings: state.settings,
				tokens: {
					...state.tokens,
					[action.meta.network]: {
						list: state.tokens[action.meta.network].list,
						balance: {
							...state.tokens[action.meta.network].balance,
							[action.meta.address]: action.meta.balance,
						},
					},
				},
			};
		case REMOVE:
			return {
				celo: state.celo,
				network: state.network,
				isConnected: state.isConnected,
				account: state.account,
				contract: state.contract,
				settings: state.settings,
				tokens: {
					...state.tokens,
					[action.meta.network]: {
						list: state.tokens[action.meta.network].list.filter((token) => token.address !== action.meta.address),
						balance: { ...state.tokens[action.meta.network].balance, [action.meta.address]: undefined },
					},
				},
			};
		default:
			return state;
	}
}
