import React from 'react';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Token, actionCreators as storeActions } from '../store/modules/dexfair';

import FormToken from './FormToken';

interface Props {
	tokenList: Token[];
	slot0: {
		label?: string;
		token: Token;
		enable: boolean;
		amount: string;
		text?: string;
		success: string;
		error: string;
	};
	slot1: {
		label?: string;
		token: Token;
		enable: boolean;
		amount: string;
		text?: string;
		success: string;
		error: string;
	};
	updateAmount: (slot: number, amount: string) => void;
	changeToken: (slot: number, token: Token) => void;
	getAllowance: (token: Token) => Promise<string>;
	setApprove: (token: Token, callback: () => Promise<void>) => void;
}

const FormTokenPair: React.FunctionComponent<Props> = (props) => {
	const { tokenList, slot0, slot1, updateAmount, changeToken, getAllowance, setApprove } = props;

	return (
		<div className="DexfairLiquidity">
			<FormToken
				tokenList={tokenList}
				token={slot0.token}
				label={slot0.label || slot0.token.symbol}
				enabled={slot0.enable}
				amount={slot0.amount}
				text={slot0.text}
				success={slot0.success}
				error={slot0.error}
				getAllowance={async (_token: Token) => {
					const amount = await getAllowance(_token);
					return amount;
				}}
				setApprove={setApprove}
				updateAmount={(_amount: string) => {
					updateAmount(0, _amount);
				}}
				changeToken={(_token: Token) => {
					changeToken(0, _token);
				}}
			/>
			<FormToken
				tokenList={tokenList}
				token={slot1.token}
				label={slot1.label || slot1.token.symbol}
				enabled={slot1.enable}
				amount={slot1.amount}
				text={slot1.text}
				success={slot1.success}
				error={slot1.error}
				getAllowance={async (_token: Token) => {
					const amount = await getAllowance(_token);
					return amount;
				}}
				setApprove={setApprove}
				updateAmount={async (_amount: string) => {
					updateAmount(1, _amount);
				}}
				changeToken={(_token: Token) => {
					changeToken(1, _token);
				}}
			/>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(FormTokenPair);
