import React from 'react';
import { Button, Modal, Row } from 'react-bootstrap';
import { Celo } from '@dexfair/celo-web-signer';
// import { TransactionResult } from '@celo/connect';
import QRCode from 'qrcode.react';
import QrReader from 'react-qr-scanner';
import CryptoJS, { AES } from 'crypto-js';
import { TxData, MetaTx } from './Types';
import firestore from '../firebase';

interface Props {
	celo: Celo;
	show: boolean;
	txData: TxData;
	updataMainBalance: (address: string) => Promise<void>;
	onHide: () => void;
}

const ModalSndTx: React.FunctionComponent<Props> = (props) => {
	const [message, setMessage] = React.useState<string>('');
	const [status, setStatus] = React.useState<number>(0); // 0: busy, 1: success, 2: error
	const [header, setHeader] = React.useState<string>('');
	const [txHash, setTxHash] = React.useState<string>('');
	const [qrString, setQrString] = React.useState<string>('');
	const [camera, setCamera] = React.useState<boolean>(false);
	const [scanDone, setScanDone] = React.useState<boolean>(false);
	const [docRef, setDocRef] = React.useState<any>(null);

	const { celo, show, txData, updataMainBalance, onHide } = props;

	React.useEffect(() => {
		async function init() {
			try {
				setQrString('');
				// console.log(txData);
				if (txData.celoTx) {
					setMessage('send transaction...');
					const tx = await celo.kit.sendTransaction(txData.celoTx);
					await waitReceipt(txData.celoTx.from, { celoTx: tx });
				} else if (txData.metaTx) {
					const contract = new celo.kit.web3.eth.Contract([txData.metaTx.abi.signer]);
					const otc = celo.kit.web3.utils.keccak256(contract.methods.otc(...txData.metaTx.parms).encodeABI());
					const metaTx: MetaTx = { ...txData.metaTx, sig: await celo.sign(otc || '', txData.metaTx.from) };

					const nonce = parseInt((Math.random() * 100000000).toString(), 10);
					const key = celo.kit.web3.utils.sha3(`metatx:key:${metaTx.abi.signer.name}:${metaTx.from}:${nonce}`);
					const index0 = celo.kit.web3.utils.sha3(`metatx:address:${metaTx.from}`);
					const index1 = celo.kit.web3.utils.sha3(`metatx:index:${metaTx.abi.signer.name}:${metaTx.from}:${nonce}`);
					if (otc && key && index0 && index1) {
						const indexA = `metatx/otc/${Buffer.from(index0.replace('0x', ''), 'hex')
							.toString('base64')
							.replaceAll('/', '@')}`;
						const indexB = `${Buffer.from(index1.replace('0x', ''), 'hex').toString('base64').replaceAll('/', '@')}`;
						firestore
							.collection(indexA)
							.doc(indexB)
							.set({
								value: AES.encrypt(JSON.stringify(metaTx), key).toString(),
								hash: '',
								deadline: txData.metaTx.parms[3],
							})
							.then(() => {
								setQrString(JSON.stringify({ method: metaTx.abi.signer.name, from: metaTx.from, nonce }));
								const doc = firestore.collection(indexA).doc(indexB);
								setDocRef(doc);
								doc.onSnapshot(
									async (docSnapshot) => {
										if (!docSnapshot.exists) {
											// eslint-disable-next-line no-console
											console.log('No such document!');
										} else {
											// eslint-disable-next-line no-console
											// console.log(`Received doc snapshot: ${JSON.stringify(docSnapshot.data())}`);
											const docData: any = docSnapshot.data();
											if (docData && docData.hash) {
												const infoTx = JSON.parse(AES.decrypt(docData.hash, key).toString(CryptoJS.enc.Utf8));
												waitReceipt(metaTx.from, { hash: infoTx.hash });
											}
										}

										// eslint-disable-next-line @typescript-eslint/no-empty-function
										doc.onSnapshot(() => {})();
										setDocRef(null);
									},
									(err) => {
										// eslint-disable-next-line no-console
										console.log(`Encountered error: ${err}`);
										// eslint-disable-next-line @typescript-eslint/no-empty-function
										doc.onSnapshot(() => {})();
										setDocRef(null);
									}
								);
							});
					}
				} else {
					setCamera(true);
				}
			} catch (e) {
				// eslint-disable-next-line no-console
				console.log(new Error(JSON.stringify(e)));
				setStatus(2);
				setHeader('alert alert-danger');
				setMessage(e.message ? e.message : e.toString());
				setTxHash('');
			}
		}
		if (show) {
			setStatus(0);
			setHeader('');
			setMessage('');
			setTxHash('');
			setCamera(false);
			setScanDone(false);
			init();
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [celo.kit, show, txData]);

	function onScanSuccess(text: string) {
		if (text && !scanDone) {
			setScanDone(true);
			const data = JSON.parse(text);
			// eslint-disable-next-line no-console
			// console.log(data, text);
			const key = celo.kit.web3.utils.sha3(`metatx:key:${data.method}:${data.from}:${data.nonce}`);
			const index0 = celo.kit.web3.utils.sha3(`metatx:address:${data.from}`);
			const index1 = celo.kit.web3.utils.sha3(`metatx:index:${data.method}:${data.from}:${data.nonce}`);

			if (key && index0 && index1) {
				const indexA = `metatx/otc/${Buffer.from(index0.replace('0x', ''), 'hex')
					.toString('base64')
					.replaceAll('/', '@')}`;
				const indexB = `${Buffer.from(index1.replace('0x', ''), 'hex').toString('base64').replaceAll('/', '@')}`;
				firestore
					.collection(indexA)
					.doc(indexB)
					.get()
					.then(async (doc) => {
						if (!doc.exists) {
							// eslint-disable-next-line no-console
							console.log('No such document!');
						} else {
							const docData: any = doc.data();
							if (docData && docData.value) {
								const metaTx = JSON.parse(AES.decrypt(docData.value, key).toString(CryptoJS.enc.Utf8));
								/*
								const tempContract = new celo.kit.web3.eth.Contract([metaTx.abi.signer]);
								const tempAddress = await celo.recover(
									celo.kit.web3.utils.keccak256(tempContract.methods.otc(...metaTx.parms).encodeABI()),
									metaTx.sig
								);
                console.log(metaTx, tempAddress);
                */
								if (metaTx.sig) {
									try {
										const contract = new celo.kit.web3.eth.Contract([metaTx.abi.sender]);
										const accounts: string[] = await celo.getAccounts();
										const tx = await celo.kit.sendTransaction({
											from: accounts[0],
											to: metaTx.to,
											data: contract.methods.otc(...metaTx.parms, metaTx.from, metaTx.sig).encodeABI(),
										});
										firestore
											.collection(indexA)
											.doc(indexB)
											.update({
												hash: AES.encrypt(
													JSON.stringify({ sender: accounts[0], hash: await tx.getHash() }),
													key
												).toString(),
											});
										await waitReceipt(accounts[0], { celoTx: tx });
									} catch (err) {
										// eslint-disable-next-line no-console
										console.log('Error getting document', err);
									}
								}
							}
						}
					})
					.catch((err) => {
						// eslint-disable-next-line no-console
						console.log('Error getting document', err);
					});
			}
		}
	}

	function onScanError(e: any) {
		setCamera(false);
		setStatus(2);
		setHeader('alert alert-danger');
		setMessage(e.message ? e.message : e.toString());
		setTxHash('');
	}

	async function waitReceipt(
		address: string | number | undefined,
		tx: { celoTx?: any /* TransactionResult */; hash?: any }
	) {
		setCamera(false);
		setQrString('');
		setMessage('send transaction...');
		const receipt = tx.celoTx ? await tx.celoTx.waitReceipt() : await celo.kit.web3.eth.getTransactionReceipt(tx.hash);
		// console.log(receipt);
		setStatus(1);
		setHeader('alert alert-success');
		setMessage('send transaction...success');
		setTxHash(receipt.transactionHash);
		if (address) {
			await updataMainBalance(address.toString());
		}
		if (txData.callback) {
			txData.callback();
		}
		// TODO: redux getBalance, setBalance
	}

	function onClose() {
		if (docRef) {
			// eslint-disable-next-line @typescript-eslint/no-empty-function
			const unsub = docRef.onSnapshot(() => {});
			unsub();
			setDocRef(null);
		}
		onHide();
	}

	function WebCam() {
		return (
			<QrReader
				style={{
					height: 320,
					width: 320,
				}}
				facingMode="rear"
				onError={onScanError}
				onScan={onScanSuccess}
			/>
		);
	}

	return (
		<div className="ModalSndTx">
			<Modal show={show} onHide={onClose} aria-labelledby="contained-modal-title-vcenter" backdrop="static" centered>
				<Modal.Header closeButton className={header}>
					<Modal.Title id="contained-modal-title-vcenter">{txData.title}</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					<p hidden={!!qrString}>{message}</p>
					<Row hidden={!qrString || camera} className="justify-content-center">
						<QRCode value={qrString} />
					</Row>
					<Row hidden={!camera} className="justify-content-center">
						{camera ? WebCam() : null}
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						hidden={status !== 1}
						variant="link"
						onClick={() => {
							window.open(`${celo.getNetwork().blockscout}/tx/${txHash}`);
						}}
					>
						Blockscout
					</Button>
					<Button onClick={onClose} disabled={status === 0}>
						Close
					</Button>
				</Modal.Footer>
			</Modal>
		</div>
	);
};

export default ModalSndTx;
