import React from 'react';
import { Button, Card, Container, Navbar, Nav, NavDropdown, Form, Image, Row, Col, InputGroup } from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import { Celo, NETWORKS } from '@dexfair/celo-web-signer';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from './store/modules';
import { Token, TokenStore, Dexfair, actionCreators as storeActions } from './store/modules/dexfair';

import './animation.css';
import Board from './components/Board';
import ModalSettings from './components/ModalSettings';
import ModalAddToken from './components/ModalAddToken';

const CONNECTS: { [key: string]: { icon: string; name: string } } = {
	usb: { icon: './ledger-512.png', name: 'Ledger USB' },
	ble: { icon: './ledger-512.png', name: 'Ledger BLE' },
	celo: { icon: './celo-512.png', name: 'Celo' },
	metamask: { icon: './metamask-512.png', name: 'MetaMask' },
};

export interface TokenConfig {
	[key: string]: Token[];
}

interface Props {
	celo: Celo;
	network: string;
	isConnected: boolean;
	account: string;
	contract: Dexfair;
	tokens: TokenStore;
	StoreActions: typeof storeActions;
}

const App: React.FunctionComponent<Props> = (props) => {
	const [initDone, setInitDone] = React.useState<boolean>(false);
	const [tokenJson, setTokenJson] = React.useState<TokenConfig>({});
	const [support, setSupport] = React.useState<{ [key: string]: boolean }>({});
	const [walletIcon, setWalletIcon] = React.useState<string>('...loading');
	const [balance, setBalance] = React.useState<{ celo: string; cusd: string }>({ celo: '', cusd: '' });
	const [showSettings, setShowSettings] = React.useState<boolean>(false);
	const [showAddToken, setShowAddToken] = React.useState<boolean>(false);

	const { celo, network, isConnected, account, contract, tokens, StoreActions } = props;

	React.useEffect(() => {
		async function init() {
			if (initDone) {
				await updateTokenList(tokenJson, network);
				await updataMainBalance(account);
			} else {
				setSupport(await celo.getSupport());
				fetch('/dexfair/config.json')
					.then((respons) => respons.json())
					.then(async (config: Dexfair) => {
						StoreActions.dexfairUpdate(config);
						fetch('/dexfair/tokens.json')
							.then((respons) => respons.json())
							.then(async (result: TokenConfig) => {
								await updateTokenList(result, network);
								setInitDone(true);
								setTokenJson(result);
							});
					});
			}
		}
		init();
		// eslint-disable-next-line
	}, [network]);

	async function login(wallet: string) {
		try {
			let result = false;
			switch (wallet) {
				case 'metamask':
					result = await celo.connectMetaMask(async (accounts: string[]) => {
						setWalletIcon(CONNECTS.metamask.icon);
						StoreActions.accountUpdate(accounts[0]);
						await updataMainBalance(accounts[0]);
					});
					break;
				case 'usb':
					result = await celo.connectLedgerUSB(async (accounts: string[]) => {
						setWalletIcon(CONNECTS.usb.icon);
						StoreActions.accountUpdate(accounts[0]);
						await updataMainBalance(accounts[0]);
					});
					break;
				case 'ble':
					result = await celo.connectLedgerBLE(async (accounts: string[]) => {
						setWalletIcon(CONNECTS.ble.icon);
						StoreActions.accountUpdate(accounts[0]);
						await updataMainBalance(accounts[0]);
					});
					break;
				default:
					celo.disconnect();
					StoreActions.networkConnect(false);
					setWalletIcon('');
					StoreActions.accountUpdate('');
					setBalance({ celo: '', cusd: '' });
					break;
			}
			StoreActions.networkConnect(result);
		} catch (error) {
			// eslint-disable-next-line no-console
			console.log(new Error(error));
			StoreActions.networkConnect(false);
		}
	}

	async function changeNetwork(selectedNetwork: string) {
		if (selectedNetwork !== network) {
			const temp = NETWORKS[selectedNetwork];
			StoreActions.networkUpdate(selectedNetwork);
			setBalance({ celo: '...loading', cusd: '...loading' });
			await celo.changeNetwork(temp);
		}
	}

	async function updateTokenList(cfg: TokenConfig, selectedNetwork: string) {
		if (tokens[selectedNetwork] && tokens[selectedNetwork].list.length === 0) {
			const gold = await celo.kit.contracts.getGoldToken();
			const stable = await celo.kit.contracts.getStableToken();
			const xgp = contract.address[selectedNetwork];
			StoreActions.tokenInsert(selectedNetwork, { icon: '/dexfair/celo.svg', symbol: 'Celo', address: gold.address });
			StoreActions.tokenInsert(selectedNetwork, { icon: '/dexfair/cusd.svg', symbol: 'cUsd', address: stable.address });
			if (xgp) {
				StoreActions.tokenInsert(selectedNetwork, { icon: '/dexfair/dexfair.png', symbol: 'XGP', address: xgp });
			}
			cfg[selectedNetwork].forEach((token) => {
				StoreActions.tokenInsert(selectedNetwork, token);
			});
		}
	}

	async function updataMainBalance(address: string) {
		if (celo.kit.web3.utils.isAddress(address)) {
			setBalance({ celo: '...loading', cusd: '...loading' });
			const total = await celo.kit.getTotalBalance(address);
			if (tokens[network] && tokens[network].list.length > 1) {
				StoreActions.tokenUpdate(network, tokens[network].list[0].address, total.CELO.toString());
				StoreActions.tokenUpdate(network, tokens[network].list[1].address, total.cUSD.toString());
			}
			setBalance({
				celo: celo.kit.web3.utils.fromWei(total.CELO.toString()),
				cusd: celo.kit.web3.utils.fromWei(total.cUSD.toString()),
			});
		}
	}

	function DrawNetwork() {
		const items = Object.keys(NETWORKS).map((select: string, index: number) => (
			<NavDropdown.Item
				key={`network_${index.toString()}`}
				value={select}
				onClick={async (e: any) => {
					await changeNetwork(e.target.text);
				}}
			>
				{select}
			</NavDropdown.Item>
		));
		const connects = Object.keys(support).map((wallet: string, index: number) => (
			<NavDropdown.Item
				key={`connect_${index.toString()}`}
				hidden={!support[wallet] || isConnected}
				onClick={async () => {
					await login(wallet);
				}}
			>
				<Image src={CONNECTS[wallet].icon} style={{ height: '18px' }} fluid />
				&nbsp;
				<span className="align-middle">{CONNECTS[wallet].name}</span>
			</NavDropdown.Item>
		));
		return (
			<NavDropdown title={network} id="collasible-nav-dropdown">
				{items}
				<NavDropdown.Divider />
				{connects}
				<NavDropdown.Item
					hidden={!isConnected}
					onClick={() => {
						setShowAddToken(true);
					}}
				>
					<span className="align-middle">
						<i className="fas fa-plus-square mr-2" />
						Add Token
					</span>
				</NavDropdown.Item>
				<NavDropdown.Item
					hidden={!isConnected}
					onClick={() => {
						setShowSettings(true);
					}}
				>
					<span className="align-middle">
						<i className="fas fa-cog mr-2" />
						Settings
					</span>
				</NavDropdown.Item>
				<NavDropdown.Divider hidden={!isConnected} />
				<NavDropdown.Item
					hidden={!isConnected}
					onClick={() => {
						login('');
					}}
				>
					<span className="align-middle">
						<i className="fas fa-sign-out-alt mr-2" />
						Logout
					</span>
				</NavDropdown.Item>
			</NavDropdown>
		);
	}

	return (
		<div className="App">
			<Navbar bg="light" expand="lg">
				<Container>
					<Navbar.Brand href="/">
						<Image src="./dexfair_00.png" style={{ height: '32px' }} fluid />
					</Navbar.Brand>
					<Navbar.Toggle aria-controls="responsive-navbar-nav" />
					<Navbar.Collapse id="responsive-navbar-nav">
						<Nav className="mr-auto" />
						<Nav>{DrawNetwork()}</Nav>
					</Navbar.Collapse>
				</Container>
			</Navbar>
			<CSSTransition in={isConnected} timeout={300} classNames="zoom" unmountOnExit>
				<Container className="my-5">
					<Card>
						<Card.Header>
							<Image src={walletIcon} style={{ height: '24px' }} fluid />
							&nbsp;
							<span className="align-middle">{account}</span>
							<span className="align-middle">
								<Button
									variant="link"
									size="sm"
									target="_blank"
									href={`${NETWORKS[network].blockscout}/address/${account}`}
								>
									<i className="fas fa-external-link-alt" />
								</Button>
							</span>
						</Card.Header>
						<Card.Body>
							<Row>
								<Col>
									<InputGroup>
										<InputGroup.Prepend>
											<InputGroup.Text>
												<Image src="./icon-celo-CELO-dark-f.svg" style={{ height: '24px' }} />
											</InputGroup.Text>
										</InputGroup.Prepend>
										<Form.Control placeholder="...loading" value={balance.celo} readOnly />
									</InputGroup>
								</Col>
								<Col>
									<InputGroup>
										<InputGroup.Prepend>
											<InputGroup.Text>
												<Image sizes="sm" src="./icon-celo-dollar-black-f.svg" style={{ height: '24px' }} />
											</InputGroup.Text>
										</InputGroup.Prepend>
										<Form.Control placeholder="...loading" value={balance.cusd} readOnly />
									</InputGroup>
								</Col>
							</Row>
						</Card.Body>
					</Card>
				</Container>
			</CSSTransition>
			<Board updataMainBalance={updataMainBalance} />
			<ModalSettings
				show={showSettings}
				onHide={() => {
					setShowSettings(false);
				}}
			/>
			<ModalAddToken
				celo={celo}
				network={network}
				tokens={tokens}
				insertToken={(token: Token) => StoreActions.tokenInsert(network, token)}
				show={showAddToken}
				onHide={() => {
					setShowAddToken(false);
				}}
			/>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(App);
