import React from 'react';
import { Card, Container, Tabs, Tab } from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import { Celo } from '@dexfair/celo-web-signer';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Dexfair, actionCreators as storeActions } from '../store/modules/dexfair';

import { TxData } from './Types';
import ModalSndTx from './ModalSndTx';
import Transfer from './Transfer';
import Exchange from './Exchange';
import DexfairExchange from './DexfairExchange';
import DexfairLiquidity from './DexfairLiquidity';
import '../animation.css';

interface Props {
	celo: Celo;
	network: string;
	account: string;
	isConnected: boolean;
	contract: Dexfair;
	updataMainBalance: (address: string) => Promise<void>;
}

const Board: React.FunctionComponent<Props> = (props) => {
	const [key, setKey] = React.useState<string | null>('transfer');
	const [gasPrice, setGasPrice] = React.useState<string>('');
	const [modalShow, setModalShow] = React.useState<boolean>(false);
	const [txData, setTxData] = React.useState<TxData>({ title: '' });

	const { celo, network, account, contract, updataMainBalance } = props;

	React.useEffect(() => {
		async function init() {
			const price = await celo.kit.web3.eth.getGasPrice();
			setGasPrice(price);
		}
		init();
		setKey('transfer');
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [network]);

	async function getAllowance(addrContract: string, addrToken: string): Promise<string> {
		if (celo.kit.web3.utils.isAddress(account) && addrContract) {
			const amount = await celo.kit.web3.eth.call({
				to: addrToken,
				data: celo.contracts.erc20.methods.allowance(account, addrContract).encodeABI(),
			});
			return amount;
		}
		return '0';
	}

	function setApprove(addrContract: string, addrToken: string, callback: () => void) {
		if (celo.kit.web3.utils.isAddress(account) && addrContract) {
			const MAX = celo.kit.web3.utils.hexToNumberString(
				'0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'
			);
			openTxModal({
				title: 'Approve',
				celoTx: {
					from: account,
					to: addrToken,
					data: celo.contracts.erc20.methods.approve(addrContract, MAX).encodeABI(),
				},
				callback,
			});
		}
	}

	function estimateFee(gas: number): string {
		return celo.kit.web3.utils.fromWei(
			celo.kit.web3.utils
				.toBN(Math.round(gas * celo.kit.connection.defaultGasInflationFactor))
				.mul(celo.kit.web3.utils.toBN(gasPrice))
		);
	}

	function openTxModal(data: TxData) {
		setTxData(data);
		setModalShow(true);
	}

	return (
		<Container className="my-5">
			<Card>
				<Card.Header>
					<div hidden={!!contract.address[network]}>
						<Tabs activeKey={key} onSelect={(k) => setKey(k)}>
							<Tab eventKey="transfer" title="Transfer" />
							<Tab eventKey="exchange" title="Exchange" />
						</Tabs>
					</div>
					<div hidden={!contract.address[network]}>
						<Tabs activeKey={key} onSelect={(k) => setKey(k)}>
							<Tab eventKey="transfer" title="Transfer" />
							<Tab eventKey="exchange" title="Exchange" />
							<Tab eventKey="dexfair" title="Dexfair" />
							<Tab eventKey="liquidity" title="Liquidity" />
						</Tabs>
					</div>
				</Card.Header>
				<Card.Body>
					<CSSTransition in={key === 'transfer'} timeout={400} classNames="fade" unmountOnExit>
						<Transfer hidden={key !== 'transfer'} estimateFee={estimateFee} openTxModal={openTxModal} />
					</CSSTransition>
					<CSSTransition in={key === 'exchange'} timeout={400} classNames="fade" unmountOnExit>
						<Exchange
							hidden={key !== 'exchange'}
							getAllowance={getAllowance}
							setApprove={setApprove}
							estimateFee={estimateFee}
							openTxModal={openTxModal}
						/>
					</CSSTransition>
					<CSSTransition in={key === 'dexfair'} timeout={400} classNames="fade" unmountOnExit>
						<DexfairExchange
							hidden={key !== 'dexfair' || !contract.address[network]}
							getAllowance={getAllowance}
							setApprove={setApprove}
							estimateFee={estimateFee}
							openTxModal={openTxModal}
						/>
					</CSSTransition>
					<CSSTransition in={key === 'liquidity'} timeout={400} classNames="fade" unmountOnExit>
						<DexfairLiquidity
							hidden={key !== 'liquidity' || !contract.address[network]}
							getAllowance={getAllowance}
							setApprove={setApprove}
							estimateFee={estimateFee}
							openTxModal={openTxModal}
						/>
					</CSSTransition>
				</Card.Body>
			</Card>
			<ModalSndTx
				celo={celo}
				show={modalShow}
				txData={txData}
				updataMainBalance={updataMainBalance}
				onHide={() => {
					setModalShow(false);
					setTxData({ title: '' });
				}}
			/>
		</Container>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(Board);
