import React from 'react';
import { Button, Form, Row, Col } from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import { debounce } from 'lodash';
import { Celo } from '@dexfair/celo-web-signer';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Token, TokenStore, Dexfair, Settings, actionCreators as storeActions } from '../store/modules/dexfair';

import { TxData } from './Types';
import FormTokenPair from './FormTokenPair';
import '../animation.css';

interface Props {
	celo: Celo;
	network: string;
	isConnected: boolean;
	account: string;
	settings: Settings;
	tokens: TokenStore;
	contract: Dexfair;
	hidden: boolean;
	getAllowance: (addrContract: string, addrToken: string) => Promise<string>;
	setApprove: (addrContract: string, addrToken: string, callback: () => Promise<void>) => void;
	estimateFee: (gas: number) => string;
	openTxModal: (txData: TxData) => void;
}

const DexfairExchange: React.FunctionComponent<Props> = (props) => {
	const [token0, setToken0] = React.useState<Token>({ icon: '', symbol: '', address: '' });
	const [token1, setToken1] = React.useState<Token>({ icon: '', symbol: '', address: '' });
	const [amount0, setAmount0] = React.useState<string>('');
	const [amount1, setAmount1] = React.useState<string>('');
	const [success0, setSuccess0] = React.useState<string>('');
	const [error0, setError0] = React.useState<string>('');
	const [otcEnable, setOtcEnable] = React.useState<boolean>(false);

	const [exchange, setExchange] = React.useState<any>(null);
	const [pair, setPair] = React.useState<{
		adr: string;
		netProfitBurn?: string;
		netProfitExchange?: string;
		tokens?: string[];
		reserves?: string[];
	}>({ adr: '' });

	const {
		celo,
		network,
		isConnected,
		account,
		tokens,
		contract,
		settings,
		hidden,
		getAllowance,
		setApprove,
		estimateFee,
		openTxModal,
	} = props;

	React.useEffect(() => {
		if (tokens[network].list.length > 1) {
			const address = contract.address[network];
			if (!exchange && address) {
				const temp = new celo.kit.web3.eth.Contract(contract.abi, address);
				setExchange(temp);
			} else if (exchange) {
				setToken0(tokens[network].list[0]);
				setToken1(tokens[network].list[1]);
				updatePair(tokens[network].list[0], tokens[network].list[1]);
			}
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [network, tokens, exchange]);

	// eslint-disable-next-line react-hooks/exhaustive-deps
	const delayedQuery = React.useCallback(debounce(getEstimate, 300), [amount0, token0, token1]);

	async function updatePair(tokenA: Token, tokenB: Token) {
		if (exchange) {
			const pairStatus = await exchange.methods.getPair([tokenA.address, tokenB.address]).call();
			setPair(pairStatus);
		} else {
			setPair({ adr: '' });
		}
	}

	function createPair() {
		openTxModal({
			title: 'Create Pair',
			celoTx: {
				from: account,
				to: exchange.options.address,
				data: exchange.methods.register([token0.address, token1.address]).encodeABI(),
			},
			callback: () => {
				// eslint-disable-next-line no-console
				console.log('Exchange');
			},
		});
	}

	function getDeadline() {
		return Math.floor(new Date().getTime() / 1000) + parseInt(settings.deadline.value, 10) * 60;
	}

	async function getEstimate(tokenSell: Token, sellAmount: string, tokenBuy: Token) {
		if (sellAmount !== '') {
			const deadline = getDeadline();
			const sell = celo.kit.web3.utils.toWei(sellAmount).toString();
			const buyAmount = await exchange.methods.getBuyTokenAmount([tokenSell.address, tokenBuy.address], sell).call();

			const buy = celo.kit.web3.utils
				.toBN(buyAmount)
				.mul(
					celo.kit.web3.utils.toBN(
						parseInt((1000 - parseInt(settings.slippage.value, 10) * 10).toString(), 10).toString()
					)
				)
				.div(celo.kit.web3.utils.toBN(1000))
				.toString();

			if (celo.kit.web3.utils.isAddress(account)) {
				try {
					const gas = await celo.kit.web3.eth.estimateGas({
						from: account,
						to: exchange.options.address,
						data: exchange.methods.exchange([tokenSell.address, tokenBuy.address], [sell, buy], deadline).encodeABI(),
					});

					if (tokenSell.address === tokens[network].list[0].address) {
						const total = celo.kit.web3.utils.toBN(sell).add(celo.kit.web3.utils.toBN(gas));
						const balance = celo.kit.web3.utils.toBN(
							tokens[network].balance[tokens[network].list[0].address] || (await celo.kit.web3.eth.getBalance(account))
						);
						if (balance.lt(total)) {
							setAmount1(celo.kit.web3.utils.fromWei(buyAmount.toString()));
							setError0('out of balance');
							return;
						}
					}
					setAmount1(celo.kit.web3.utils.fromWei(buyAmount.toString()));
					setSuccess0(`fee : ${estimateFee(gas)}`);
					setError0('');
				} catch (e) {
					setAmount1(celo.kit.web3.utils.fromWei(buyAmount.toString()));
					setError0(e.message ? e.message : e.Error.toString());
				}
			} else {
				setAmount1(celo.kit.web3.utils.fromWei(buyAmount.toString()));
				setError0('');
			}
		} else {
			setAmount1('');
			setSuccess0('');
			setError0('');
		}
	}

	function onExchange() {
		const deadline = getDeadline();
		const sell = celo.kit.web3.utils.toWei(amount0);
		const buy = celo.kit.web3.utils
			.toBN(celo.kit.web3.utils.toWei(amount1))
			.mul(celo.kit.web3.utils.toBN(parseInt(settings.slippage.value, 10) * 10))
			.div(celo.kit.web3.utils.toBN(1000))
			.toString();

		openTxModal({
			title: 'Exchange',
			celoTx: {
				from: account,
				to: exchange.options.address,
				data: exchange.methods.exchange([token0.address, token1.address], [sell, buy], deadline).encodeABI(),
			},
			callback: () => {
				// eslint-disable-next-line no-console
				console.log('Exchange');
			},
		});
	}

	async function onOTCCreate() {
		const deadline = getDeadline();
		const sell = celo.kit.web3.utils.toWei(amount0);
		const buy = celo.kit.web3.utils
			.toBN(celo.kit.web3.utils.toWei(amount1))
			.mul(celo.kit.web3.utils.toBN(parseInt(settings.slippage.value, 10) * 10))
			.div(celo.kit.web3.utils.toBN(1000))
			.toString();

		const chainId = await celo.kit.web3.eth.getChainId();
		const otcSigner = exchange.methods.otc(chainId, [token0.address, token1.address], [sell, buy], deadline);
		const otcSender = exchange.methods.otc(
			chainId,
			[token0.address, token1.address],
			[sell, buy],
			deadline,
			account,
			'0x'
		);

		openTxModal({
			title: 'Exchange - OTC (Maker)',
			metaTx: {
				abi: {
					// eslint-disable-next-line no-underscore-dangle
					signer: JSON.parse(JSON.stringify(otcSigner._method)),
					// eslint-disable-next-line no-underscore-dangle
					sender: JSON.parse(JSON.stringify(otcSender._method)),
				},
				parms: otcSigner.arguments,
				from: account,
				to: exchange.options.address,
			},
			callback: () => {
				// eslint-disable-next-line no-console
				console.log('Exchange - OTC (Maker)');
			},
		});
	}

	function onOTCScan() {
		openTxModal({
			title: 'Exchange - OTC (Taker)',
			callback: () => {
				console.log('Exchange - OTC');
			},
		});
	}

	return (
		<div className="DexfairExchange" hidden={hidden}>
			<FormTokenPair
				tokenList={tokens[network].list}
				slot0={{
					label: 'Sell',
					token: token0,
					enable: celo.kit.web3.utils.isAddress(pair.adr) && pair.adr !== '0x0000000000000000000000000000000000000000',
					amount: amount0,
					success: success0,
					error: error0,
				}}
				slot1={{ label: 'Buy (estimated)', token: token1, enable: false, amount: amount1, success: '', error: '' }}
				getAllowance={async (token: Token) => {
					const result = await getAllowance(exchange.options.address, token.address);
					return result;
				}}
				setApprove={(token: Token, callback) => {
					setApprove(exchange.options.address, token.address, callback);
				}}
				updateAmount={(slot: number, amount: string) => {
					switch (slot) {
						case 0:
							setAmount0(amount);
							delayedQuery(token0, amount, token1);
							break;
						default:
							break;
					}
					return { amount: '', success: '', error: '' };
				}}
				changeToken={(slot: number, token: Token) => {
					switch (slot) {
						case 0:
							if (token1.address === token.address) {
								setToken1(token0);
								setToken0(token);
							} else {
								setToken0(token);
								updatePair(token, token1);
							}
							break;
						case 1:
							setToken1(token);
							if (token0.address === token.address) {
								setToken0(token1);
								setToken1(token);
							} else {
								setToken1(token);
								updatePair(token0, token);
							}
							break;
						default:
							break;
					}
					setAmount0('');
					setAmount1('');
					setSuccess0('');
					setError0('');
				}}
			/>
			<fieldset disabled={!isConnected}>
				<CSSTransition in={!otcEnable} timeout={400} classNames="fade" unmountOnExit>
					<Row hidden={otcEnable}>
						<Col>
							<Button
								variant={isConnected ? 'primary' : 'secondary'}
								disabled={
									!isConnected ||
									((error0 !== '' || amount0 === '' || success0 === '') &&
										pair.adr !== '0x0000000000000000000000000000000000000000')
								}
								block
								onClick={() => {
									if (pair.adr !== '0x0000000000000000000000000000000000000000') {
										onExchange();
									} else {
										createPair();
									}
								}}
							>
								{pair.adr !== '0x0000000000000000000000000000000000000000' ? 'Exchange' : 'Create Pair'}
							</Button>
						</Col>
					</Row>
				</CSSTransition>
				<CSSTransition in={otcEnable} timeout={400} classNames="fade" unmountOnExit>
					<Row hidden={!otcEnable}>
						<Col>
							<Button
								variant={isConnected ? 'primary' : 'secondary'}
								disabled={!isConnected || error0 !== '' || amount0 === '' || success0 === ''}
								block
								onClick={onOTCCreate}
							>
								Maker
							</Button>
						</Col>
						<Col>
							<Button variant={isConnected ? 'primary' : 'secondary'} block onClick={onOTCScan}>
								Taker
							</Button>
						</Col>
					</Row>
				</CSSTransition>
				<Form>
					<Form.Check
						id="otc-switch"
						type="switch"
						label="OTC"
						value="true"
						className="mt-3"
						checked={otcEnable}
						onChange={(e: any) => {
							setOtcEnable(e.target.checked);
						}}
					/>
				</Form>
			</fieldset>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(DexfairExchange);
