import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';
import { Celo } from '@dexfair/celo-web-signer';
import { Token, TokenStore } from '../store/modules/dexfair';

interface Props {
	celo: Celo;
	network: string;
	tokens: TokenStore;
	insertToken: (token: Token) => void;
	show: boolean;
	onHide: () => void;
}

const ModalAddToken: React.FunctionComponent<Props> = (props) => {
	const [header, setHeader] = React.useState<string>('');
	const [address, setAddress] = React.useState<string>('');
	const [busy, setBusy] = React.useState<boolean>(false);
	const [success, setSuccess] = React.useState<boolean>(false);
	const [token, setToken] = React.useState<Token>({ icon: '', address: '', symbol: '', name: '' });

	const { celo, network, tokens, insertToken, show, onHide } = props;

	React.useEffect(() => {
		setSuccess(false);
		setAddress('');
		setToken({ icon: '', address: '', symbol: '', name: '' });
	}, [show]);

	return (
		<div className="ModalAddToken">
			<Modal show={show} onHide={onHide} aria-labelledby="contained-modal-title-vcenter" backdrop="static" centered>
				<Modal.Header closeButton className={header}>
					<Modal.Title id="contained-modal-title-vcenter">Add Token</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					<Form>
						<Form.Group>
							<Form.Text>Symbol</Form.Text>
							<InputGroup>
								<Form.Control readOnly type="string" placeholder="" value={token.symbol} />
							</InputGroup>
						</Form.Group>
						<Form.Group>
							<Form.Text>Name</Form.Text>
							<InputGroup>
								<Form.Control readOnly type="string" placeholder="" value={token.name} />
							</InputGroup>
						</Form.Group>
						<Form.Group>
							<Form.Text>Address</Form.Text>
							<InputGroup>
								<Form.Control
									type="string"
									placeholder="0x"
									value={address}
									onChange={(e) => {
										setAddress(e.target.value);
									}}
								/>
								<InputGroup.Append>
									<Button
										hidden={!celo.kit.web3.utils.isAddress(address)}
										disabled={busy}
										onClick={async () => {
											setBusy(true);
											setSuccess(false);
											const symbol = await celo.kit.web3.eth.call({
												to: address,
												data: celo.contracts.erc20.methods.symbol().encodeABI(),
											});
											if (symbol !== '0x') {
												const name = await celo.kit.web3.eth.call({
													to: address,
													data: celo.contracts.erc20.methods.name().encodeABI(),
												});
												if (name !== '0x') {
													setSuccess(tokens[network].balance[token.address] === undefined);
													setToken({
														icon: '',
														address,
														symbol: `${celo.kit.web3.eth.abi.decodeParameter('string', symbol)}` || address,
														name: `${celo.kit.web3.eth.abi.decodeParameter('string', name)}`,
													});
												}
											}
											setBusy(false);
										}}
									>
										<i className="fas fa-search-plus" />
									</Button>
								</InputGroup.Append>
							</InputGroup>
						</Form.Group>
					</Form>
				</Modal.Body>
				<Modal.Footer>
					<Button
						onClick={() => {
							if (success) {
								if (tokens[network].balance[token.address] === undefined) {
									insertToken(token);
								}
							}
							onHide();
						}}
					>
						{success ? 'Add & Close' : 'Close'}
					</Button>
				</Modal.Footer>
			</Modal>
		</div>
	);
};

export default ModalAddToken;
