import React from 'react';
import { Button, Form } from 'react-bootstrap';
import { debounce } from 'lodash';
import { Celo } from '@dexfair/celo-web-signer';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { StoreState } from '../store/modules';
import { Token, TokenStore, actionCreators as storeActions } from '../store/modules/dexfair';

import { TxData } from './Types';
import FormToken from './FormToken';

interface Props {
	celo: Celo;
	network: string;
	isConnected: boolean;
	account: string;
	tokens: TokenStore;
	hidden: boolean;
	estimateFee: (gas: number) => string;
	openTxModal: (txData: TxData) => void;
}

const Transfer: React.FunctionComponent<Props> = (props) => {
	const [token, setToken] = React.useState<Token>({ icon: '', symbol: '', address: '' });
	const [to, setTo] = React.useState<string>('');
	const [amount, setAmount] = React.useState<string>('');
	const [error0, setError0] = React.useState<string>('');
	const [success1, setSuccess1] = React.useState<string>('');
	const [error1, setError1] = React.useState<string>('');
	const [disable, setDisable] = React.useState<boolean>(false);

	const { celo, network, isConnected, account, tokens, hidden, estimateFee, openTxModal } = props;

	React.useEffect(() => {
		if (tokens[network].list.length > 0 && token.address !== tokens[network].list[0].address) {
			setToken(tokens[network].list[0]);
			setTo('');
		} else if (tokens[network].list.length === 0 && token.address) {
			setToken({ icon: '', symbol: '', address: '' });
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [network, tokens]);

	// eslint-disable-next-line react-hooks/exhaustive-deps
	const delayedQuery = React.useCallback(debounce(estimate, 300), [amount, token, to]);

	async function estimate(value: string) {
		if (value !== '' && celo.kit.web3.utils.isAddress(account) && celo.kit.web3.utils.isAddress(to)) {
			try {
				if (token.address === tokens[network].list[0].address) {
					const temp = celo.kit.web3.utils.toWei(value);
					const gas = await celo.kit.web3.eth.estimateGas({
						from: account,
						to,
						value,
					});
					const total = celo.kit.web3.utils.toBN(temp).add(celo.kit.web3.utils.toBN(gas));
					const balance = celo.kit.web3.utils.toBN(
						tokens[network].balance[tokens[network].list[0].address] || (await celo.kit.web3.eth.getBalance(account))
					);
					if (balance.lt(total)) {
						setDisable(true);
						setSuccess1('');
						setError1('out of balance');
						return;
					}
					setDisable(false);
					setSuccess1(`fee : ${estimateFee(gas)}`);
					setError1('');
				} else {
					const gas = await celo.kit.web3.eth.estimateGas({
						from: account,
						to: token.address,
						data: celo.contracts.erc20.methods.transfer(to, celo.kit.web3.utils.toWei(value)).encodeABI(),
					});
					setDisable(false);
					setSuccess1(`fee : ${estimateFee(gas)}`);
					setError1('');
				}
			} catch (e) {
				// eslint-disable-next-line no-console
				console.log(new Error(e));
				setDisable(true);
				setSuccess1('');
				setError1(e.message ? e.message : e.Error.toString());
			}
		} else {
			setDisable(true);
			setSuccess1('');
			setError1('');
		}
	}

	function onTransfer() {
		if (celo.kit.web3.utils.isAddress(account) && to !== '' && amount !== '' && celo.kit.web3.utils.isAddress(to)) {
			if (token.address === tokens[network].list[0].address) {
				openTxModal({
					title: 'Transfer',
					celoTx: { from: account, to, value: celo.kit.web3.utils.toWei(amount) },
					callback: () => {
						// eslint-disable-next-line no-console
						console.log('Transfer 0');
					},
				});
			} else {
				openTxModal({
					title: 'Transfer',
					celoTx: {
						from: account,
						to: token.address,
						data: celo.contracts.erc20.methods.transfer(to, celo.kit.web3.utils.toWei(amount)).encodeABI(),
					},
					callback: () => {
						// eslint-disable-next-line no-console
						console.log('Transfer 1');
					},
				});
			}
		}
	}

	return (
		<div className="Transfer" hidden={hidden}>
			<Form>
				<fieldset disabled={!isConnected}>
					<Form.Group>
						<Form.Text>To</Form.Text>
						<Form.Control
							type="text"
							placeholder="0x"
							isInvalid={to.length > 0 && !celo.kit.web3.utils.isAddress(to)}
							isValid={to.length > 0 && celo.kit.web3.utils.isAddress(to)}
							value={to}
							onChange={(e) => {
								if (celo.kit.web3.utils.isAddress(e.target.value)) {
									setError0('');
									setTo(e.target.value);
								} else {
									setError0(e.target.value.length > 0 ? 'is not address' : '');
									setTo(e.target.value);
								}
							}}
						/>
						<Form.Control.Feedback type="invalid">{error0}</Form.Control.Feedback>
						<Form.Text className="text-muted" hidden={error0.length > 0}>
							&nbsp;
						</Form.Text>
					</Form.Group>
					<FormToken
						tokenList={tokens[network].list}
						token={token}
						label="Amount"
						enabled={celo.kit.web3.utils.isAddress(to)}
						amount={amount}
						success={success1}
						error={error1}
						getAllowance={async (_token: Token) => {
							return '99999999999999999999999999999999';
						}}
						updateAmount={(_amount: string) => {
							setAmount(_amount);
							delayedQuery(_amount);
						}}
						changeToken={(_token: Token) => {
							setAmount('');
							setToken(_token);
						}}
					/>
					<Button
						variant={isConnected ? 'primary' : 'secondary'}
						onClick={onTransfer}
						disabled={
							!amount ||
							disable ||
							error0 !== '' ||
							error1 !== '' ||
							success1 === '' ||
							!celo.kit.web3.utils.isAddress(to)
						}
						block
					>
						Transfer
					</Button>
				</fieldset>
			</Form>
		</div>
	);
};

export default connect(
	({ dexfairStore }: StoreState) => ({ ...dexfairStore }),
	(dispatch) => ({
		StoreActions: bindActionCreators(storeActions, dispatch),
	})
)(Transfer);
